import BScroll from "better-scroll";

export default {
  components: {},
  data() {
    return {
      dots: [],
      currentDotsIndex: 0
    };
  },
  props: {
    // 
    loop: {
      type: Boolean,
      default: true
    },
    // 
    autoPlay: {
      type: Boolean,
      default: true
    },
    // 
    delay: {
      type: Number,
      default: 3000
    }
  },
  methods: {
    // (sliderGroup)
    _setSliderWidth(isResize) {
      // 
      this.children = this.$refs.sliderGroup.children;
      // console.log(this.children) // (5) [div, div, div, div, div]

      // slider
      let width = 0;
      let sliderWidth = this.$refs.slider.clientWidth;
      //  classwidth
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i];
        this.addClass(child, "slider-item");

        child.style.width = sliderWidth + "px";
        width += sliderWidth;
      }

      if (this.loop && !isResize) {
        width += 2 * sliderWidth;
      }

      this.$refs.sliderGroup.style.width = width + "px";
    },
    /**
     *  class
     */
    hasClass(el, className) {
      let reg = new RegExp("(^|\\s)" + className + "(\\s|$)");
      return reg.test(el.className);
    },

    /**
     *  class
     */
    addClass(el, className) {
      if (this.hasClass(el, className)) {
        return;
      }
      let newClass = el.className.split(" ");
      newClass.push(className);
      el.className = newClass.join(" ");
    },

    // 
    _initSlider() {
      this.slider = new BScroll(this.$refs.slider, {
        click: true,
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: true,
        snapLoop: this.loop,
        snapThreshold: 0.3,
        snapSpeed: 400
      });

      this.slider.on("scrollEnd", () => {
        let nowIndex = this.slider.getCurrentPage().pageX;
        //  -1
        if (this.loop) {
          nowIndex -= 1;
        }
        this.currentDotsIndex = nowIndex;
        // 
        if (this.autoPlay) {
          clearTimeout(this.timer);
          this._initPlay();
        }
      });
    },
    // 
    _initDots() {
      this.dots = new Array(this.children.length);
    },
    // 
    _initPlay() {
      let nextIndex = this.currentDotsIndex + 1;
      if (this.loop) {
        nextIndex += 1;
      }
      this.timer = setTimeout(() => {
        this.slider.goToPage(nextIndex, 0, 400);
      }, this.delay);
    }
  },
  created() {},
  mounted() {
    setTimeout(() => {
      this._setSliderWidth();
      this._initDots();
      this._initSlider();

      if (this.autoPlay) {
        this._initPlay();
      }

      // 
      window.addEventListener("resize", () => {
        if (!this.slider) {
          return;
        }
        this._setSliderWidth(true);
        this.slider.refresh();
      });
    }, 20);
  },
  destroyed() {
    // 
    clearTimeout(this.timer);
  }
};



